.header {
  margin-top: .5rem;
  padding-left: 10px;
}

.header ul li {
  list-style: none;
  margin: 0;
  padding: .5rem 1rem;
  border-radius: 3px;
}

.header ul li:hover {
  background-color: #444;
}

.header a:hover {
  text-decoration: none;
}

.header h3 a {
  color: white;
}

.header h3 a:hover {
  color: #00bc8c;
}

.lds-double-ring {
  width:100%;
  height:100%
}



@keyframes lds-double-ring {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-webkit-keyframes lds-double-ring {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes lds-double-ring_reverse {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}
@-webkit-keyframes lds-double-ring_reverse {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}
.lds-double-ring {
  position: relative;
}
.lds-double-ring div {
  position: absolute;
  width: 120px;
  height: 120px;
  top: 40px;
  left: 40px;
  border-radius: 50%;
  border: 14px solid #000;
  border-color: #0a0301 transparent #0a0301 transparent;
  -webkit-animation: lds-double-ring 2s linear infinite;
  animation: lds-double-ring 2s linear infinite;
}
.lds-double-ring div:nth-child(2) {
  width: 88px;
  height: 88px;
  top: 56px;
  left: 56px;
  border-color: transparent #3d6aa2 transparent #3d6aa2;
  -webkit-animation: lds-double-ring_reverse 2s linear infinite;
  animation: lds-double-ring_reverse 2s linear infinite;
}
.lds-double-ring {
  width: 91px !important;
  height: 91px !important;
  -webkit-transform: translate(-45.5px, -45.5px) scale(0.455) translate(45.5px, 45.5px);
  transform: translate(-45.5px, -45.5px) scale(0.455) translate(45.5px, 45.5px);
}

.ng-scope {
  width: 100%;
  display: flex;
  justify-content: center;

}
.error-indicator {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  color: coral
}

.error-indicator .boom {
  font-size: 32px;
}

.error-indicator img {
  width: 200px;
  border-radius: 20px;
}
.random-planet {
  display: flex;
  padding: 1rem;
}

.random-planet .planet-image {
  width: 150px;
  height: 150px;
  border-radius: 10px;
  margin-right: 1rem;
}

.random-planet .list-group {
  margin: 1rem 0 0 1rem;
}

.random-planet .list-group-item {
  padding: .25rem;
}

.random-planet .list-group-item .term {
  margin-right: .5rem;
}
.item-list .list-group-item {
  cursor: pointer;
}

.item-list .list-group-item:hover {
  background-color: #444;
}
.person-details {
  display: flex;
  flex-direction: row;
  padding: 1rem;
}

.person-details .person-image {
  width: 30%;
  height: 30%;
  border-radius: 10px;
}

.person-details .list-group-item {
  padding: .25rem;
}

.person-details .list-group-item .term{
  margin-right: .5rem;
}
